.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

input {
  border-radius: 8px;
  height: 20px;
  margin-bottom: 24px;
}

.input-area {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 2px solid white;
  padding: 32px;
}

.send-notification-button {
  width: 80px;
  height: 32px;
  color: red;
  font-weight: bold;
  font-size: 16px;
}
